var state = {
  loadingList: [],
  isLoading: true
};
var mutations = {
  // 开启loading
  PUSH_LOADING: function PUSH_LOADING(states, playload) {
    states.loadingList.push({
      text: playload || '加载中..'
    });
  },
  // 递归删除loading
  SHIFT_LOADING: function SHIFT_LOADING(states) {
    states.loadingList.shift();
  },
  // 控制是否需要loading
  SHOULD_LOADING: function SHOULD_LOADING(states, isShould) {
    states.isLoading = isShould;
  }
};
var getters = {
  isLoading: function isLoading(states) {
    return states.loadingList.length > 0;
  },
  loadingText: function loadingText(states) {
    return states.loadingList.length > 0 ? states.loadingList[0].text : null;
  }
};
var actions = {
  /**
   * 开启loading
   * @param states
   * @param playload
   */
  openLoading: function openLoading(states, playload) {
    states.commit('PUSH_LOADING', playload);
  },

  /**
   * 关闭loading
   * @param contaxt
   */
  closeLoading: function closeLoading(states) {
    states.commit('SHIFT_LOADING');
  }
};
export default {
  namespaced: true,
  state: state,
  mutations: mutations,
  getters: getters,
  actions: actions
};