import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var store = new Vuex.Store({
  mutations: {},
  actions: {}
});
/**
 * 查询模块
 */

var modulesContext = require.context('@/store/modules', true, /\.js$/);
/**
 * 创建模块函数
 * @param fileArr
 */


function createStoreModules(fileArr) {
  fileArr.keys().forEach(function (modules) {
    store.registerModule(modules.replace(/(^\.\/)|(\.js$)/g, ''), fileArr(modules).default);
  });
}
/**
 * 创建模块
 */


createStoreModules(modulesContext);
export default store;